@import"https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap";

  @import url('https://fonts.googleapis.com/css2?family=Foldit&family=Sigmar&display=swap');


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
    /* background-image: url(../images/background.png); */
}

.Head-div {
    width: 100%;
    height: 3.7rem;
    background-color: rgba(255, 255, 255, 0.959);
    padding: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 5px 3px rgb(199, 199, 199);
    position: fixed;
}

.Head-div .Header div h2 {
    font-size: 30px;
    color: #ffffff;
    cursor: pointer;
    font-family: 'Foldit', cursive;
    font-family: 'Sigmar', cursive;
}

.Header {
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    
}

.Header ul {

    display: flex;
    min-width: 200px;
    width: 500px;
    justify-content: space-between;
    text-decoration: none;
}

.Header ul li a {
    font-size: 15px;
    color: #454046;
    font-weight: bold;
    font-family: 'Noto Sans', sans-serif;
    font-family: 'Poppins', sans-serif;
}


.Header ul li a:hover {

    font-size: 15px;
    color: #ffffff;
    /* box-shadow: rgba(255, 255, 255, 0.4) 0px 0px 0px 2px, rgba(214, 97, 223, 0.65) 0px 4px 6px -1px, rgba(255, 255, 255, 0.08) 0px 1px 0px inset; */
}

.Header .icon {
    display: none;
    color: gray;
}

.Header-res {
    display: none;
}

.Head-div .display-hide {
    display: none;
}

.Header .User {
    display: flex;
    justify-content: space-around;
    padding-right: 20px;
    
}

.Header .User a {
    font-size: 15px;
    color: #454046;
    font-weight: bold;
    font-family: 'Noto Sans', sans-serif;
}

.Header .User a i {
    color: #fd5631;
}

.Header .User .SignIn{
    margin: 20px 8px;
}

.Header .User .SignUp{
    background-color: #fd5631;
    color: white;
    margin: 10px 8px;
    margin-right: -50px;
    padding: 10px 8px;
    border-radius: 20px;
    
    
}

.Header .User .SignUp i{
    color: white;
}




.Head-div{
    margin: 2px 0px;
  color: #fff;
  background: linear-gradient(
      90deg,
      rgba(252, 176, 69, 1) 0%,
      rgba(253, 29, 29, 0.6) 25%,
      rgba(131, 58, 180, 1) 50%,
      rgb(197, 160, 222) 100%
      
    ), radial-gradient(circle, rgba(255, 255, 255, 1) 0%, rgba(0, 0, 0, 1) 28%);
  
  background-origin: padding-box, content-box;
  background-repeat: no-repeat;
}




@media screen and (max-width: 1400px) {
    .Header ul{
        margin-left: 2rem;
    }

    .Header .User {
        width: 19%;
        display: flex;
        justify-content: space-around;
    
    }
}


@media screen and (max-width: 1100px) {
    .Header .User {
        width: 22%;
        display: flex;
        justify-content: space-around;
    
    }
}

@media screen and (max-width:1000px) {


    

    .Header ul li a {
        display: none;
    }

    .Header ul li a.icon {
        
        display: block;
        font-size: 35px;
    }

    .Header .User {
        margin-right: -5px;
    }

    .Header-res {
        transition: 0.2s;
        display: block;
        position: absolute;
        background: white;
        width: 100%;
        height: auto;
        top: 56px;
        padding: 1rem 0.3rem;
        right: 0px;
        
    }

    .Header-res ul .Res-li {
        
        margin: 6px;
        text-align: left;
        border-radius: 5px;
        border: 1px solid rgba(190, 190, 190, 0.397);
        padding: 10px ;
        
    }

    .Header-res ul li a {
        font-weight: bold;
        color: #454056;
        font-family: 'Noto Sans', sans-serif;
        font-size: 16px;
    }


    .Header-res .Social-Media {
        display: flex;
        width: 70%;
        justify-content: space-between;
        padding-left: 15rem; 
    }

    .Header-res .Social-Media li a i{
        font-size: 30px;
        margin-top: 5px;
        
    }
    .Header-res .Social-Media li a .fa-instagram{
        color: white;
        background: #f09433; 
        background: -moz-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%); 
        background: -webkit-linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%); 
        background: linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%); 
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f09433', endColorstr='#bc1888',GradientType=1 );
        border-radius: 3px;
        padding-left: 2px;
        padding-right: 2px;
  
    }

    .Header-res .Social-Media li a .fa-facebook{
        color: blue;
        background-color: #fff;
    }

    .Header-res .Social-Media li a .fa-twitter{
        color: #1ca4f4;
        background-color: white;
        
    }

}


@media screen and (max-width:900px) {
    .Header ul li a.icon{
        margin-right: 15px;
    }

    .Header .User {
        width: 35%;
        display: flex;
        justify-content: space-around;
    
    }
}

@media screen and (max-width:769px) {
    .Header .User {
        display: none;
    }
}
